import common from '../../common/common.js'

export default {
	data() {
		return {
			banner: {},
			listData: [],
			last_id: "",
			reload: false,
			page:0,
			pageSize:10,
			swiperList: [],
			serchName:''
		}
	},
	onLoad(option) {
		this.serchName = option.name;
		this.getBanner();
		this.getList(this.serchName);
	},
	//======================监听用户下拉动作============================================
	onPullDownRefresh() {
		this.reload = true;
		this.last_id = "";
		this.getBanner();
		this.getList();
	},
	//======================页面上拉触底事件的处理函数=====================================
	onReachBottom() {
		this.getList(this.serchName);
	},
	methods: {
		//======================进入banner详情============================================
		gobannerDetail(index){
			uni.navigateTo({
				url:'detail?url='+this.swiperList[index].guid
			})
		},
		//======================获取banner列表============================================
		getBanner() {
			uni.request({
				url:common.domain+'/bannerlist',
				success: (res) => {
					// console.log('=============>>'+JSON.stringify(res.data['data']));
					this.swiperList = res.data;
				}
			})
		},
		//======================获取文章列表============================================
		getList(cateName) {
			var domain = common.domain
			uni.request({
				url:domain + '/articlelist?cat_name='+cateName+'&limit='+this.pageSize+'&page='+this.page,
				method:"GET",
				success: (res) => {
					let list = res.data
					this.listData = this.reload ? list : this.listData.concat(list)
					this.reload = false
					this.page+=1
				}
			})
		},
		//======================进入详情页============================================
		goDetail: function (e) {
			uni.navigateTo({
				url:'detail?url='+e.guid
			})
		},
		
	},
}
